import { Err, Ok, Result } from 'ts-res';
import { KameleoonError, KameleoonException } from 'src/kameleoonError';
import { TargetingType } from 'src/targeting/types';
import { BrowserCondition } from './browserCondition';
import { ConversionCondition } from './conversionCondition';
import { CustomDataCondition } from './customDataCondition';
import { DeviceCondition } from './deviceCondition';
import { ExclusiveExperimentCondition } from './exclusiveExperimentCondition';
import { PageTitleCondition } from './pageTitleCondition';
import { PageUrlCondition } from './pageUrlCondition';
import { SdkLanguageCondition } from './sdkLanguageCondition';
import { TargetExperimentCondition } from './targetExperimentCondition';
import { ConditionDataType, ConditionType } from './types';
import { VisitorCodeCondition } from './visitorCodeCondition';

export class ConditionFactory {
  static createCondition(
    data: ConditionDataType,
  ): Result<ConditionType, KameleoonError> {
    const { targetingType } = data;

    switch (targetingType) {
      case TargetingType.CUSTOM_DATUM:
        return Ok(new CustomDataCondition(data));
      case TargetingType.EXCLUSIVE_EXPERIMENT:
        return Ok(new ExclusiveExperimentCondition());
      case TargetingType.TARGET_EXPERIMENT:
        return Ok(new TargetExperimentCondition(data));
      case TargetingType.DEVICE_TYPE:
        return Ok(new DeviceCondition(data));
      case TargetingType.BROWSER:
        return Ok(new BrowserCondition(data));
      case TargetingType.PAGE_URL:
        return Ok(new PageUrlCondition(data));
      case TargetingType.PAGE_TITLE:
        return Ok(new PageTitleCondition(data));
      case TargetingType.CONVERSIONS:
        return Ok(new ConversionCondition(data));
      case TargetingType.VISITOR_CODE:
        return Ok(new VisitorCodeCondition(data));
      case TargetingType.SDK_LANGUAGE:
        return Ok(new SdkLanguageCondition(data));
      default:
        return Err(
          new KameleoonError(
            KameleoonException.TargetingCondition,
            targetingType,
          ),
        );
    }
  }
}
